@import 'src/styles/variables.scss';

.cart {
  position: fixed;
  height: 100vh;
  width: toRem(400);
  box-sizing: border-box;
  right: toRem(-400);
  top: 0;
  z-index: 20;

  background-color: white;
  border-left: $border-std;

  transition: right $anim-fast;

  display: grid;
  grid-template-rows: auto 1fr auto;

  &.cart-active {
    right: 0;
  }
}


@media only screen and (max-width: $media-mobile-XS) {
  .cart {
    width: 100%;
    right: -100%;
  }
}