@import 'src/styles/variables.scss';

.hmm-pics {
  position: relative;
  width: 40vw;
}

.hmm-pics {
  background-color: $color-grey;

  .hmm-pics-container {
    position: relative;
    height: 100%;
    width: 100%;
    box-sizing: border-box;

    display: grid;
    grid-template-rows: 1fr toRem(80);

    .hmm-title {
      padding: 0 toRem(34);
      border-top: $border-std;

      display: flex;
      justify-content: space-between;
      align-items: center;

      .hmm-title-title{
        height: toRem(18);
        font-family: $font-std;
        font-size: toRem(14);
        font-weight: 700;
      }

      .hmm-title-vendor{
        height: toRem(18);
        font-family: $font-std;
        font-size: toRem(14);
        color: $color-dark-grey-2;
      }
    }
  }
}
