@import 'src/styles/variables.scss';

.mmm-submenu {
  background-color: white;
  width: 100%;
  margin-top: toRem(25);
  padding: toRem(12) toRem(20);
  box-sizing: border-box;

  .mmm-submenu-header {
    padding: toRem(10) 0;
    font-size: toRem(18);
    text-transform: uppercase;

    &.mmm-submenu-header-bold {
      font-weight: 700;
    }
  }

  .mmm-submsubmenu-header {
    display: grid;
    grid-template-columns: 1fr auto;
    padding: toRem(10) 0;

    .mmm-submsubmenu-header-text {
      width: 100%;
      font-size: toRem(18);
      text-transform: uppercase;
    }

    .mmm-submsubmenu-header-icon {
        padding-left: toRem(32);
    }
  }

  .mmm-subsubmenu-item {
    padding: toRem(10) toRem(20);
    font-size: toRem(16);

    &.mmm-subsubmenu-item-ul {
      text-decoration: underline;
    }

    &:first-child {
      padding-top: toRem(5);
    }
    &:last-child {
      padding-top: toRem(5);
    }
  }

  .mmm-subsubmenu-orphan {
    .mmm-subsubmenu-item {
      padding: toRem(10) 0;
    }
  }
}
