@import 'src/styles/variables.scss';

.generic-module {
  height: toRem(715);
  width: 100%;
  position: relative;

  .gm-bk {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .generic-module-mobile {
    display: none;
  }

  .generic-module-content {
    position: absolute;
    width: 100%;
    left: 0;
    top: toRem(170);

    display: flex;
    flex-direction: column;
    align-items: center;

    .gmc-sub {
      font-size: toRem(13);
      margin-bottom: toRem(25);
    }

    .gmc-header {
      font-family: $font-rauna;
      font-size: toRem(45);
      line-height: toRem(45);
      margin-bottom: toRem(35);
    }

    .gmc-buttons {
      display: flex;
      justify-content: center;
      align-content: center;

      .gmc-button {
        margin: 0 toRem(13);
      }
    }
  }
}

@media only screen and (max-width: $media-mobile-small) {
  .generic-module {
    height: toRem(625);

    .generic-module-mobile {
      display: block;
    }

    .generic-module-desktop {
      display: none;
    }

    .generic-module-content {
      top: toRem(75);

      .gmc-sub {
        font-size: toRem(15);
      }

      .gmc-header {
        font-family: $font-rauna;
        font-size: toRem(32);
        line-height: toRem(32);
        margin-bottom: toRem(35);
      }

      .gmc-buttons {
        display: flex;
        flex-direction: column;

        .gmc-button {
          margin: toRem(12) 0;
        }

        .bttn-wrapper {
          margin-bottom: toRem(25);
        }
      }
    }
  }
}
