@import 'src/styles/variables.scss';

.stories-module {
  min-height: toRem(900);
  width: 100%;
  position: relative;

  .slider-progress {
    border-top: none;
    height: toRem(120);
  }

  .sf-img {
    height: 100%;
    width: 100%;
    border: $border-std;
    box-sizing: border-box;
    overflow: hidden;

    .sf-img-content {
      height: 100%;
      width: 100%;

      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      transition: transform $anim-fast;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  .stories-featured {
    height: toRem(700);
    width: 100%;
    padding: toRem(50) toRem(32);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: 1fr auto;
    column-gap: toRem(50);

    font-size: toRem(14);

    .sf-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      width: toRem(405);

      .sf-title {
        font-family: $font-rauna;
        font-size: toRem(40);
        line-height: toRem(46);
        white-space: normal;
      }

      .sf-readmore {
        height: toRem(100);
        border-top: toRem(1) solid black;
        border-bottom: toRem(1) solid black;
        margin-top: toRem(40);
        box-sizing: border-box;

        transition: border $anim-fast;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: toRem(36);
        text-transform: uppercase;
        font-weight: 700;

        .icon-arrow-right {
          font-size: toRem(50);
        }

        &:hover {
          border-top: toRem(4) solid black;
          border-bottom: toRem(4) solid black;
        }
      }
    }

    .sf-cat {
      text-transform: uppercase;
    }
  }

  .stories-group {
    position: relative;
    height: toRem(240);
    width: 100%;
    padding: 0 0 0 toRem(32);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: toRem(130) 1fr;

    .stories-double-border {
      box-sizing: border-box;
      border-top: 1px solid black;
      border-bottom: 1px solid black;
    }

    .stories-all {
      padding: 0;
      height: 100%;

      display: flex;
      align-items: center;

      transition: border $anim-fast;

      &:hover {
        border-top: 2px solid black;
        border-bottom: 2px solid black;
      }

      .stories-all-content {
        height: toRem(200);

        display: flex;
        justify-content: space-between;
        flex-direction: column;

        span {
          font-size: toRem(30);
          line-height: toRem(25);
        }

        p {
          font-size: toRem(14);
          text-transform: uppercase;
          padding: 0;
          margin: 0;
        }
      }
    }

    // 32 - 130 - 25 - X - 32
    .stories-carousel {
      height: 100%;
      width: calc(100% - #{toRem(210)});
      right: toRem(32);
      position: absolute;

      .stories-carousel-item {
        height: toRem(240);
        padding: toRem(18) 0;
        width: auto !important;
        margin-left:  toRem(35);

        display: grid !important;
        grid-template-columns: 1fr 1fr;
        column-gap: toRem(22);

        .sc-img {
          position: relative;
          height: 100%;
          width: 100%;
          border: $border-std;
          box-sizing: border-box;
          overflow: hidden;

          display: flex;
          justify-content: center;
          align-items: center;

          .sc-img-content {
            height: 100%;
            width: 100%;

            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;

            transition: transform $anim-fast;

            &:hover {
              transform: scale(1.05);
            }
          }
        }

        .sc-cnt {
          height: 100%;
          width: 100%;
          box-sizing: border-box;

          display: grid;
          grid-template-rows: auto 1fr auto;
          row-gap: toRem(10);

          p {
            font-size: toRem(12);
            padding: 0;
            margin: 0;
          }

          .sc-cnt-header {
            font-weight: 700;
          }
        }
      }
    }
  }
}

.sf-mobile-only {
  display: none !important;
}

@media only screen and (max-width: $media-mobile-small) {
  .sf-mobile-only {
    display: block;

    &.sf-readmore {
      height: toRem(75);
      margin-bottom: toRem(42);

      font-size: toRem(24);
      font-weight: 700;

      .icon-arrow-right {
        font-size: toRem(50);
      }

      &:hover {
        border-top: toRem(4) solid black;
        border-bottom: toRem(4) solid black;
      }
    }
  }

  .stories-module {
    padding: toRem(30) toRem(20) 0 toRem(20);
    box-sizing: border-box;

    .slider-progress {
      height: toRem(100);
    }

    .sf-img {
      .sf-img-content {
        height: toRem(225);
      }
    }

    .stories-featured {
      height: auto;
      padding: 0;

      grid-template-rows: auto auto;
      grid-template-columns: 1fr;
      row-gap: toRem(25);

      .sf-content {
        width: 100%;
        display: block;

        .sf-date {
          margin-bottom: toRem(32);
        }

        .sf-title {
          font-family: $font-rauna;
          font-size: toRem(32);
          line-height: toRem(37);
          margin-bottom: toRem(32);
        }

        .sf-author {
          margin-bottom: toRem(50);
        }

        .sf-body {
          .sf-readmore {
            display: none;
          }
        }
      }

      .sf-cat {
        text-transform: uppercase;
      }
    }

    .stories-group {
      height: auto;
      padding: 0;
      width: 100%;

      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;

      .stories-double-border {
        box-sizing: border-box;
        border-top: 1px solid black;
        border-bottom: 1px solid black;
      }

      .stories-all {
        padding: 0;
        height: toRem(50);

        display: flex;
        align-items: flex-start;
        justify-content: center;
        border-top: none;
        border-bottom: none;

        transition: none;

        &:hover {
          border-top: none;
          border-bottom: none;
        }

        .stories-all-content {
          height: auto;
          width: 100%;

          display: flex;
          justify-content: space-between;
          flex-direction: row;

          span {
            font-size: toRem(30);
            line-height: toRem(25);
          }

          p {
            font-size: toRem(14);
            text-transform: uppercase;
            padding: 0;
            margin: 0;
          }
        }
      }

      // 32 - 130 - 25 - X - 32
      .stories-carousel {
        height: auto;
        width: calc(100vw - #{toRem(40)});
        right: 0;
        position: relative;

        .stories-carousel-group {
          display: grid;
          column-gap: 0;
          width: 100%;

          .stories-carousel-item {
            height: auto;
            padding: toRem(28) 0 toRem(21) 0;
            width: 100%;

            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto auto;
            row-gap: toRem(25);

            .sc-img {
              height: toRem(250);
            }

            .sc-cnt {
              height: auto;

              display: grid;
              grid-template-rows: auto 1fr auto;
              row-gap: toRem(8);

              p {
                font-size: toRem(12);
                padding: 0;
                margin: 0;
              }

              .sc-cnt-header {
                margin-bottom: toRem(10);
                font-size: 1rem;
              }
            }
          }
        }
      }
    }
  }

  .slider-progress {
    height: toRem(85) !important;

    .slider-progress-container {
      width: 100% !important;
    }
  }
}
