@import 'src/styles/variables.scss';

.story-page {
  width: 100%;
  padding: toRem(80) toRem(20);
  box-sizing: border-box;

  .story-wrapper {
    width: 100%;
    max-width: toRem(980);
    margin: 0 auto;

    img {
      width: 100%;
      margin: 0 auto;
    }

    h1 {
      font-family: $font-rauna;
      font-size: toRem(50);
    }

    .break {
      width: 100%;
      height: 1px;
      border-bottom: $border-std;
      margin-bottom: toRem(24);
    }

    .story-double {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: toRem(30);
    }
  }
}

.story-collection {
  .story-collection-header {
    width: 100%;
    text-align: center;
    font-size: toRem(50);
    font-family: $font-rauna;
    margin-bottom: toRem(55);
  }
  .story-collection-slider {
    border-top: $border-std;
    box-sizing: border-box;
  }
}

@media only screen and (max-width: $media-mobile) {
  .story-page {
    width: 100%;
    padding: toRem(50) toRem(20);
    box-sizing: border-box;

    .story-wrapper {
      width: 100%;
      max-width: toRem(980);
      margin: 0 auto;

      h1 {
        font-family: $font-rauna;
        font-size: toRem(50);
      }

      .break {
        width: 100%;
        height: 1px;
        border-bottom: $border-std;
        margin-bottom: toRem(24);
      }

      .story-double {
        display: grid;
        grid-template-columns: 1fr;
        column-gap: toRem(30);
      }
    }
  }

  .story-collection {
    .story-collection-header {
      width: 100%;
      text-align: center;
      font-size: toRem(40);
      font-family: $font-rauna;
      margin-bottom: toRem(55);
    }
  }
}
