@import 'src/styles/variables.scss';

.collections-filters {
  width: 0;
  box-sizing: border-box;
  height: auto;
  overflow: hidden;
  transition: width $anim-fast;
  position: relative;

  &.collections-filters-active {
    width: toRem(375);
  }

  .collection-filters-content {
    height: auto;
    min-height: toRem(500);
    width: toRem(375);
    position: relative;
  }
}



@media only screen and (max-width: $media-mobile) {

  .collections-filters {
    width: 0;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    position: fixed;
    z-index: 30;
    top: 0;
    left: 0;
    background-color: white;
  
    &.collections-filters-active {
      width: 100vw;
    }
  
    .collection-filters-content {
      height: auto;
      min-height: toRem(500);
      width: 100%;
      position: relative;
    }
  }
  
}
