@import 'src/styles/variables.scss';

.filter-category {
  width: 100%;
  height: auto;
  padding-right: toRem(50);
  margin-bottom: toRem(30);
  box-sizing: border-box;

  position: relative;

  p {
    margin: 0;
    padding: 0;
    font-size: toRem(18);
  }

  .filter-title {
    padding-bottom: toRem(10);
    margin-bottom: toRem(17);
    border-bottom: $border-std;

    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      text-transform: uppercase;
    }

    span {
      font-size: toRem(14);
    }
  }

  .filter-list {
    max-height: toRem(0);
    overflow-y: auto;
    overflow-x: visible;
    transition: max-height $anim-fast;

    &.filter-list-active {
      max-height: toRem(330);
    }

    &.filter-list-colors {
      display: grid;
      justify-content: space-between;
      flex-wrap: wrap;
      grid-template-columns: repeat(auto-fit, 10%);
      column-gap: toRem(30);
      row-gap: toRem(30);
      box-sizing: border-box;
    }
  }

  .filter-item-color {
    height: toRem(32);
    width: toRem(32);
    clip-path: polygon(calc(100% - 14px) 0, 100% 14px, 100% 100%, 0 100%, 0 0);
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;

    .filter-item-color-child {
      height: toRem(30);
      width: toRem(30);
      clip-path: polygon(
        calc(100% - 13px) 0,
        100% 13px,
        100% 100%,
        0 100%,
        0 0
      );

      display: flex;
      align-items: center;
      justify-content: center;
    }

    span {
      font-size: toRem(12);
      padding-top: toRem(3);
      padding-right: toRem(3);
    }
  }

  .filter-item {
    &:first-child {
      margin-top: 0;
    }

    margin: 0 0 toRem(18) 0;

    display: flex;
    align-items: center;

    .filter-item-check {
      width: toRem(18);
      height: toRem(18);
      margin-right: toRem(12);
      box-sizing: border-box;
      border: $border-std;
      background-color: white;

      display: flex;
      align-items: center;
      justify-content: center;

      &.filter-item-check-active {
        border: 1px solid black;
        background-color: $color-btn-gold;

        span {
          font-size: toRem(10);
        }
      }
    }
  }

  .filter-item-slider {
    width: 95%;
    height: toRem(100);
    margin: 0 auto 0 0;
    overflow: visible;

    .filter-item-slider-track.filter-item-slider-track-1 {
      background: black;
    }

    .filter-item-slider-track {
      position: relative;
      background: #d5d8df;
      top: 20px;
      height: 4px;
    }

    .filter-item-slider-thumb {
      cursor: pointer;
      position: absolute;
      z-index: 20;
      background: #ffffff;
      border: 1px solid black;
      border-radius: 100%;
      box-sizing: border-box;
      display: block;

      top: toRem(10);
      width: toRem(24);
      outline: none;
      height: toRem(24);
      line-height: 38px;

      span {
        position: absolute;
        top: 25px;
        left: toRem(-14);
        text-align: center;
        width: toRem(50);
        background-color: white;
      }
    }
  }
}

.collections-mobile-filters {
  display: none;
}

@media only screen and (max-width: $media-mobile) {
  .collections-mobile-filters {
    padding: toRem(40) toRem(20);
    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
      margin: 0;
      padding: 0;
      font: $font-rauna;
      font-size: toRem(28);
    }

    span {
      font-size: toRem(20);
      font-weight: 700;
    }
  }

  .filter-category {
    padding: 0 toRem(20);

    p {
      margin: 0;
      padding: 0;
      font-size: toRem(18);
    }

    .filter-title {
      padding-bottom: toRem(10);
      margin-bottom: toRem(17);
      border-bottom: $border-std;

      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        text-transform: uppercase;
      }

      span {
        font-size: toRem(14);
      }
    }

    .filter-list {
      max-height: toRem(0);
      overflow-y: auto;
      overflow-x: visible;
      transition: max-height $anim-fast;

      &.filter-list-active {
        max-height: toRem(1000);
      }

      &.filter-list-colors {
        max-width: toRem(300);
        overflow-x: hidden;
        display: grid;
        justify-content: space-between;
        flex-wrap: wrap;
        grid-template-columns: repeat(auto-fit, 20%);
        column-gap: toRem(20);
        row-gap: toRem(20);
        box-sizing: border-box;

        .filter-item-color {
          display: inline-flex;

          .filter-item-color-child {
            display: inline-flex;
          }
        }
      }
    }

    .filter-item {
      &:first-child {
        margin-top: 0;
      }

      margin: 0 0 toRem(18) 0;

      display: flex;
      align-items: center;

      .filter-item-check {
        width: toRem(18);
        height: toRem(18);
        margin-right: toRem(12);
        box-sizing: border-box;
        border: $border-std;
        background-color: white;

        display: flex;
        align-items: center;
        justify-content: center;

        &.filter-item-check-active {
          border: 1px solid black;
          background-color: $color-btn-gold;

          span {
            font-size: toRem(10);
          }
        }
      }
    }

    .filter-item-slider {
      width: 90%;
      height: toRem(100);
      margin: 0 auto 0 0;
      overflow: visible;

      .filter-item-slider-track.filter-item-slider-track-1 {
        background: black;
      }

      .filter-item-slider-track {
        position: relative;
        background: #d5d8df;
        top: 20px;
        height: 4px;
      }

      .filter-item-slider-thumb {
        cursor: pointer;
        position: absolute;
        z-index: 20;
        background: #ffffff;
        border: 1px solid black;
        border-radius: 100%;
        box-sizing: border-box;
        display: block;

        top: toRem(10);
        width: toRem(24);
        outline: none;
        height: toRem(24);
        line-height: 38px;

        span {
          position: absolute;
          top: 25px;
          left: toRem(-14);
          text-align: center;
          width: toRem(50);
          background-color: white;
        }
      }
    }
  }
}
