@import 'src/styles/variables.scss';

$width: calc(100vw - 8vw - #{toRem(475)} - 10vw);

.product-images {
  position: absolute;
  left: 0;
  top: toRem(60);

  width: $width;
  max-width: toRem(900);
  height: auto;

  .product-images-imgs {
    width: $width;
    max-width: toRem(900);
    height: toRem(600);
    background-color: $color-grey-6;
    position: relative;

    clip-path: polygon(calc(100% - 90px) 0, 100% 90px, 100% 100%, 0 100%, 0 0);

    .car-item {
      background-color: $color-grey-6;
    }

    .pi-img {
      width: 80%;
      height: toRem(600);

      mix-blend-mode: multiply;

      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  .product-images-btn {
    position: absolute !important;
    top: toRem(305);
    z-index: 2;

    span {
      font-size: toRem(30);
    }

    &.pib-right {
      right: toRem(20);
    }

    &.pib-left {
      left: toRem(20);
    }
  }

  .product-images-list {
    margin-top: toRem(15);
    width: 100%;

    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .pil-item-wrapper {
      background-color: $color-grey-6;
      margin: toRem(5);
    }

    .pil-item {
      height: toRem(100);
      width: toRem(100);

      mix-blend-mode: multiply;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;

      opacity: 0.5;
      transition: opacity $anim-fast;

      &.pil-item-active {
        opacity: 1;
      }
    }
  }
}



@media only screen and (max-width: $media-mobile) {
  .product-images {
    position: relative;
    width: 100%;
    max-width: 100%;
    top: 0;
  
    .product-images-imgs {
      width: 100%;
      max-width: 100%;
      height: toRem(350);
      clip-path: polygon(calc(100% - 50px) 0, 100% 50px, 100% 100%, 0 100%, 0 0);
  
      .pi-img {
        width: 80%;
        height: toRem(350);
      }
    }
  
    .product-images-btn {
      top: toRem(160);
      span {
        font-size: toRem(30);
      }
      &.pib-right {
        right: toRem(20);
      }
      &.pib-left {
        left: toRem(20);
      }
    }
  
    .product-images-list {
      margin: toRem(10) 0;
  
      .pil-item {
        height: toRem(75);
        width: toRem(75);
  
        opacity: 0.5;
        transition: opacity $anim-fast;
  
        &.pil-item-active {
          opacity: 1;
        }
      }
    }
  }
}