@import 'src/styles/variables.scss';

.collections-module {
  height: toRem(550);
  background-color: $color-grey-2;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: toRem(1);
}

.collection-module-component {
  height: 100%;
  width: 100%;
  background-color: $color-grey;
  position: relative;

  .cmc-img {
    position: absolute;
    top: 20%;
    height: 60%;
    width: 100%;
    mix-blend-mode: multiply;

    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    transition: transform $anim-std;

    &:hover {
      transform: scale(1.1);
    }
  }

  .cmc-header {
    position: absolute;
    left: toRem(45);
    bottom: toRem(30);
    z-index: 2;

    font-family: $font-rauna;
    font-size: toRem(28);
  }
}

@media only screen and (max-width: $media-mobile-XS) {
  .collections-module {
    height: auto;
    background-color: $color-grey-2;

    display: grid;
    grid-template-rows: repeat(2, #{toRem(300)});
    grid-template-columns: 1fr;
    column-gap: toRem(1);
    row-gap: toRem(1);
  }

  .collection-module-component {
    .cmc-header {
      left: toRem(20);
      bottom: toRem(25);

      font-size: toRem(25);
    }
  }
}
