@import 'src/styles/variables.scss';

.terms-of-service {
  padding: toRem(94) toRem(150);

  h2 {
    margin-top: toRem(120);
  }

  p {
    font-size: toRem(16);
    line-height: toRem(28);
  }

  h3 {
    margin-top: toRem(45);
  }
}

@media only screen and (max-width: $media-mobile) {
  .terms-of-service {
    padding: toRem(50) toRem(20) toRem(120) toRem(20);

    h2 {
      margin-top: toRem(120);
    }

    p {
      font-size: toRem(16);
      line-height: toRem(28);
    }

    h3 {
      margin-top: toRem(45);
    }
  }
}
