@import 'src/styles/variables.scss';

.footer {
  border-top: $border-std;
  padding: toRem(60) toRem(30) toRem(48) toRem(30);

  display: grid;
  grid-template-columns: 1fr repeat(4, toRem(250));
  column-gap: toRem(16);
  grid-template-rows: 1fr auto auto;

  font-size: toRem(14);
  line-height: toRem(30);
  color: $color-dark-grey-3;

  .ft-hover-text {
    transition: color $anim-fast;

    &:hover {
      color: black;
    }
  }

  .ft-logo {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2;

    .ft-logo-subtext {
      color: black;
      line-height: toRem(14);
    }

    img {
      width: toRem(145);
    }
  }

  .ft-sm {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;

    display: flex;
    .ft-sm-icon {
      margin-right: toRem(16);
      span {
        font-size: toRem(18);
      }
    }
  }

  .ft-cw {
    grid-row-start: 3;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 2;

    margin-top: toRem(3);
    font-size: toRem(12);
    line-height: toRem(12);
  }

  .ft-content {
    .ft-cnt-head {
      text-transform: uppercase;
      margin-bottom: toRem(25);
    }
  }

  .ft-content-likelihood {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 2;
    grid-column-end: 3;
  }

  .ft-content-shop {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 3;
    grid-column-end: 4;
  }

  .ft-content-store {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 4;
    grid-column-end: 5;
  }

  .ft-content-legal {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 5;
    grid-column-end: 6;
  }
}

@media only screen and (max-width: 1400px) {
  .footer {
    border-top: $border-std;
    padding: toRem(60) toRem(30) toRem(48) toRem(30);

    display: grid;
    grid-template-columns: 1fr repeat(4, toRem(160));
  }
}

@media only screen and (max-width: $media-mobile) {
  .footer {
    grid-template-columns: repeat(2, auto);
    column-gap: toRem(16);
    grid-template-rows: repeat(5, auto);
    row-gap: toRem(48);

    .ft-logo {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 1;
      grid-column-end: 3;
    }

    .ft-sm {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 3;
    }

    .ft-cw {
      grid-row-start: 5;
      grid-row-end: 6;
      grid-column-start: 1;
      grid-column-end: 3;
    }

    .ft-content-likelihood {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 2;
    }

    .ft-content-shop {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 2;
      grid-column-end: 3;
    }

    .ft-content-store {
      grid-row-start: 4;
      grid-row-end: 5;
      grid-column-start: 1;
      grid-column-end: 2;
    }

    .ft-content-legal {
      grid-row-start: 4;
      grid-row-end: 5;
      grid-column-start: 2;
      grid-column-end: 3;
    }
  }
}
