@import 'src/styles/variables.scss';

.page-about-us {
  position: relative;
  min-height: 500px;
  padding-top: toRem(180);
  top: 0;
  box-sizing: border-box;
  border-bottom: $border-std;

  .au-img {
    position: absolute;
    z-index: -1;
    height: toRem(631);
    width: 77vw;
    top: toRem(60);
    right: 0;
  }

  .au-content-wrapper {
    position: relative;
    margin-top: 0;
    margin-bottom: toRem(105);
    width: toRem(775);
    height: auto;
    background-color: black;

    clip-path: polygon(calc(100% - 85px) 0, 100% 85px, 100% 100%, 0 100%, 0 0);

    border: 1px solid black;

    .au-content {
      background-color: white;
      padding: toRem(100) toRem(125);
      box-sizing: border-box;
      clip-path: polygon(
        calc(100% - 84px) 0,
        100% 84px,
        100% 100%,
        0 100%,
        0 0
      );

      h1 {
        margin: 0 0 toRem(50) 0;
        font-size: toRem(54);
        line-height: toRem(54);
      }
      p {
        margin: 0 0 toRem(24) 0;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .page-about-us {
    padding-top: 0;

    .au-img {
      position: relative;
      height: toRem(264);
      width: 100%;
      top: 0;
    }

    .au-content-wrapper {
      position: relative;
      margin-top: toRem(60);
      margin-bottom: toRem(60);
      width: 100%;
      background-color: white;
      clip-path: none;
      border: none;

      .au-content {
        background-color: transparent;
        padding: 0 toRem(20);
        clip-path: none;

        h1 {
          margin: 0 0 toRem(30) 0;
          font-size: toRem(54);
          line-height: toRem(54);
        }
        p {
          margin: 0 0 toRem(24) 0;
        }
      }
    }
  }
}
