@import 'src/styles/variables.scss';

.header-menu {
  font-family: $font-std;
  font-size: toRem(13);

  display: grid;
  grid-template-columns: repeat(3, auto);
  column-gap: toRem(20);
}

.hm-option {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  .hm-p {
    margin: 0 toRem(8);
    padding: 0;
    text-transform: uppercase;
  }
}

.hm-cart {
  .hm-p {
    font-weight: bold;
  }
}

.hm-cart-counter {
  height: toRem(16);
  width: toRem(16);

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: $color-highlight;
  border-radius: toRem(20);

  font-size: toRem(12);
  font-weight: bold;
}
