@import 'src/styles/variables.scss';
.brands-header {
  height: toRem(496);
  position: relative;

  .brands-header-mobile-img {
      display: none;
  }

  .brands-header-img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
  }

  .bh-content {
    display: flex;
    align-items: center;
    padding: 0 toRem(42);
    box-sizing: border-box;
    width: 100%;
    height: 100%;

    h1 {
      margin: 0;
      padding: 0;
      font-size: toRem(50);
      line-height: toRem(50);
      margin-bottom: toRem(20);
    }

    h3 {
      margin: 0;
      padding: 0;
      font-size: toRem(16);
      line-height: toRem(27);
      font-weight: normal;
      font-family: $font-std;
      max-width: toRem(480);
    }
  }
}


@media only screen and (max-width: $media-mobile) {
    .brands-header {
        min-height: toRem(500);
        height: auto;
      
        .brands-header-mobile-img {
            display: block;
            height: toRem(367);
            width: 100%;
        }
      
        .brands-header-img {
            display: none;
        }
      
        .bh-content {
          display: block;
          padding: toRem(30) toRem(20) toRem(60) toRem(20);
          height: auto;
          background-color: $color-grey;
      
          h1 {
            margin: 0;
            padding: 0;
            font-size: toRem(50);
            line-height: toRem(55);
            margin-bottom: toRem(30);
          }
      
          h3 {
            margin: 0;
            padding: 0;
            font-size: toRem(16);
            line-height: toRem(27);
            max-width: 100%;
          }
        }
      }
}