@import 'src/styles/variables.scss';

.shipping-returns {
  padding: toRem(94) toRem(150);

  .shipping-grey-text {
    color: #646464;
    margin-top: toRem(120);
  }

  h2 {
    margin-top: toRem(120);
  }

  p {
      font-size: toRem(16);
      line-height: toRem(28);
  }

  h3 {
      margin-top: toRem(45);
  }
}


@media only screen and (max-width: $media-mobile) {
  .shipping-returns {
    padding: toRem(50) toRem(20);
    
    .less-gap {
      margin-top: toRem(60);
    }

    h1 {
      margin-bottom: 0;
    }
  
    .shipping-grey-text {
      color: #646464;
      margin: toRem(120) 0;
    }
  }
}