@import 'src/styles/variables.scss';

.mobile-menu-header{
    position: relative;
    height: $height-header;
    width: 100%;
    padding: 0 toRem(20);
    border-bottom: $border-std;
    box-sizing: border-box;

    display: grid;
    grid-template-columns: 1fr auto auto;
    column-gap: toRem(18);


    .hm-cart{
        justify-content: flex-start;
        .hm-p{
            margin-left: 0;
        }
    }

    .mobile-menu-header-exit{
        display: flex;
        align-items: center;
        
        span{
            font-weight: bold;
        }
    }
}