@import 'src/styles/variables.scss';
#mobile-menu{
  z-index: 50;
}
.mobile-menu-container {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 50;
  visibility: hidden;

  transition: opacity $anim-std, visibility $anim-std;

  &.mobile-menu-container-active {
    opacity: 1;
    visibility: visible;
  }

  .mobile-menu-backdrop {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100vh;
    width: 100vw;
  }
}

#mobile-menu{
  display: none;
}

@media only screen and (max-width: $media-mobile) {
  #mobile-menu{
    display: block;
  }
}