@import 'src/styles/variables.scss';


.hmm-coll {
    width: 100%;
    box-sizing: border-box;

    display: flex;
    justify-content: center;

    background-color: $color-grey;
    border-left: $border-std;
    border-right: $border-std;

    .hmm-coll-content{
      margin: toRem(64) 0;
      width: 95%;

      display: flex;
      justify-content: space-evenly;

      .hmm-coll-col-grp {
        margin-bottom: toRem(30);
      }
    }

    .hmm-coll-brands{
      margin: toRem(64) 0;
      width: 80%;

      .hmm-coll-brands-content{
        margin: 0;
        width: 100%;
  
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        column-gap: toRem(30);
        justify-content: space-between;
  
        .hmm-coll-brnd-grp{
          display: grid;
          grid-template-columns: toRem(30) auto;
        }

      }
    }
  }
