@import 'src/styles/variables.scss';

.account-address-content {
  position: relative;
  min-height: 200px;
  width: 100%;

  .loader-container {
    width: 60%;
    height: toRem(50);
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
  }

  p {
    margin: 0;
    padding: 0;
  }
}


@media only screen and (max-width: $media-mobile) {
  .account-address-content {
    min-height: 100px;
  
    .loader-container {
      width: 60%;
      height: 50px;
      position: absolute;
      top: 0;
      left: 0;
      pointer-events: none;
    }
  
    p {
      margin: 0;
      padding: 0;
    }
  }
}