@import 'src/styles/variables.scss';

.product-card {
  min-height: toRem(540);
  width: 100%;
  box-sizing: border-box;

  border-left: 1px solid $color-grey-2;
  margin-left: -1px;
  box-sizing: border-box;

  display: inline-grid;
  grid-template-rows: toRem(424) 1fr;
  row-gap: toRem(1);

  &.product-chrome-mod {
    margin-left: 0px;
  }

  &.product-cart-variant {
    min-height: toRem(660);
  }

  &.product-card-lite {
    min-height: toRem(385);
    height: toRem(385);
    grid-template-rows: toRem(300) toRem(85);

    .product-card-content {
      padding: 0 toRem(20);
      min-height: toRem(0);

      display: flex;
      justify-content: space-between;
      align-items: center;

      .pc-header {
        .pc-name {
          font-weight: 700;
        }
        .pc-cart {
          display: none;
        }
      }

      .pc-prod {
        align-items: center;
        .pc-price {
          display: none;
        }
      }
    }
  }

  .product-image {
    height: 100%;
    width: 100%;
    position: relative;
    background-color: white;

    .product-image-container {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;

      opacity: 0;
      visibility: hidden;

      transition: opacity $anim-fast, visibility $anim-fast;

      &.pic-active {
        opacity: 1;
        visibility: visible;
      }
    }

    .product-image-label {
      position: absolute;
      height: toRem(24);
      width: toRem(120);

      border: 1px solid black;

      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $color-btn-gold;

      span {
        font-size: toRem(14);
      }
    }
  }

  .product-card-content {
    height: 100%;
    width: 100%;
    padding: toRem(18.5) toRem(32);
    min-height: toRem(135);

    box-sizing: border-box;
    background-color: $color-grey;

    display: grid;
    grid-template-rows: 1fr toRem(30);
    row-gap: toRem(15);

    &.product-card-content-var-size {
      height: toRem(150);
    }

    &.product-card-content-var {
      grid-template-rows: 1fr auto toRem(30);
      row-gap: toRem(3);
    }

    .pc-header {
      width: 100%;

      white-space: normal;
      display: grid;
      grid-template-columns: 1fr auto;
      column-gap: toRem(20);

      .pc-name {
        width: 100%;
        font-size: toRem(16);
        line-height: toRem(18);
        max-height: toRem(36);
        font-weight: 500;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: normal;
        word-break: break-word;
      }

      .pc-brand {
        font-size: toRem(12);
        line-height: toRem(15);
        color: $color-dark-grey-2;
      }
    }

    .pc-variants {
      height: toRem(24);
      width: 100%;

      display: flex;
      align-items: center;

      .pc-variants-img {
        height: toRem(24);
        width: toRem(24);
        margin-right: toRem(15);
        box-sizing: border-box;

        opacity: 0.5;

        transition: opacity $anim-fast;

        &.pc-variants-img-active {
          opacity: 1;
          border: 1px solid black;
        }

        background-color: white;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
      }

      .pc-variants-plus {
        background-color: black;
        border-radius: toRem(80);
        width: toRem(16);
        height: toRem(16);
        text-align: center;
        line-height: toRem(16);
        font-weight: 700;
        font-size: toRem(15);
        color: $color-grey;
        margin-right: toRem(5);

        display: flex;
        align-items: center;
        justify-content: center;

        span {
          height: auto;
          width: auto;
        }
      }

      p {
        margin: 0;
        padding: 0;
        font-size: toRem(18);
        line-height: toRem(18);
      }
    }

    .pc-prod {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;

      .pc-price {
        line-height: toRem(28);
      }
    }
  }
}
