@import 'src/styles/variables.scss';

.location-module {
  width: 100%;
  background-color: $color-grey-2;
  column-gap: toRem(1);
  display: grid;

  .location {
    width: 100%;
    height: auto;
    background-color: white;
    background-color: $color-grey;

    .location-img {
      width: 100%;
      height: toRem(430);

      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }

    .location-content {
      background-color: $color-grey;
      padding: toRem(25);
      box-sizing: border-box;
      height: auto;
      position: relative;

      .location-header {
        font-family: $font-rauna;
        font-size: toRem(25);
        line-height: toRem(28);
        margin-bottom: toRem(15);
      }

      .location-text {
        display: flex;
        justify-content: space-between;

        .location-items {
          display: flex;
          .location-item {
            margin-right: toRem(40);

            span {
              font-size: toRem(14);
              display: block;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .location-module {
    row-gap: toRem(1);
    grid-template-columns: 1fr !important;

    .location {
      .location-img {
        height: toRem(325);
      }

      .location-content {
        padding: toRem(25) toRem(20);
        height: auto;

        .location-header {
          font-size: toRem(20);
          margin-bottom: toRem(20);
        }

        .location-text {
          justify-content: space-between;

          .location-items {
            display: block;
            .location-item {
              margin-right: toRem(0);
              span {
                font-size: toRem(12);
                display: block;
              }
            }
          }
        }
      }
    }
  }
}
