@import 'src/styles/variables.scss';

.activate-page {
  height: toRem(612);
  display: flex;
  justify-content: center;

  .activate-page-wrapper {
    width: toRem(725);

    .act-p-header {
      margin-top: toRem(120);
      font-size: toRem(36);
      margin-bottom: toRem(60);
    }

    .act-p-input {
      margin-bottom: toRem(54);
    }

    .act-p-submit {
      display: flex;
      margin-bottom: toRem(10);
      align-items: center;

      .act-p-cancel {
        color: $color-text-grey;
        width: toRem(125);
        text-align: center;
        margin-left: toRem(12);
        font-size: toRem(18);
        text-decoration: underline;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile-small) {
  .activate-page {
    height: auto;
    display: block;
    padding: 0 13vw;
    box-sizing: border-box;

    .activate-page-wrapper {
      width: 100%;
      padding: toRem(60) 0 toRem(80) 0;

      .act-p-header {
        margin-top: 0;
        font-size: toRem(30);
        line-height: toRem(40);
        margin-bottom: toRem(60);
      }

      .act-p-submit {
        justify-content: space-between;
        flex-wrap: wrap;

        .act-p-cancel {
          width: auto;
          padding: 0;
          margin: toRem(10) 0;
          height: 100%;
        }
      }
    }
  }
}
