@import 'src/styles/variables.scss';

.stories-page {
  min-height: 500px;
  position: relative;

  .stories-header {
    padding: toRem(84) toRem(32) toRem(34) toRem(32);
    font-family: $font-rauna;
    font-size: toRem(55);
  }

  .sf-img {
    height: 100%;
    width: 100%;
    border: $border-std;
    box-sizing: border-box;
    overflow: hidden;

    .sf-img-content {
      height: 100%;
      width: 100%;

      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      transition: transform $anim-fast;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  .stories-featured {
    height: toRem(700);
    width: 100%;
    padding: toRem(50) toRem(32);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: 1fr auto;
    column-gap: toRem(50);

    font-size: toRem(14);

    .sf-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      width: toRem(405);

      .sf-title {
        font-family: $font-rauna;
        font-size: toRem(40);
        line-height: toRem(46);
        white-space: normal;
        margin-bottom: toRem(10);
      }

      .sf-readmore {
        height: toRem(100);
        border-top: toRem(1) solid black;
        border-bottom: toRem(1) solid black;
        margin-top: toRem(40);
        box-sizing: border-box;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: toRem(36);
        text-transform: uppercase;
        font-weight: 700;

        .icon-arrow-right {
          font-size: toRem(50);
        }

        &:hover {
          border-top: toRem(2) solid black;
          border-bottom: toRem(2) solid black;
        }
      }
    }

    .sf-cat {
      text-transform: uppercase;
    }
  }

  .stories-list {
    padding: toRem(34) toRem(32) toRem(34) toRem(32);
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: toRem(36);
    row-gap: toRem(56);

    .stories-item {
      height: toRem(200);
      padding: toRem(18) 0;
      width: 100%;
      border-top: 1px solid black;
      border-bottom: 1px solid black;

      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: toRem(22);

      .sc-img {
        position: relative;
        height: 100%;
        width: 100%;
        border: $border-std;
        box-sizing: border-box;
        overflow: hidden;

        display: flex;
        justify-content: center;
        align-items: center;

        .sc-img-content {
          height: 100%;
          width: 100%;

          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;

          transition: transform $anim-fast;

          &:hover {
            transform: scale(1.05);
          }
        }
      }

      .sc-cnt {
        height: 100%;
        width: 100%;
        box-sizing: border-box;

        display: grid;
        grid-template-rows: auto 1fr auto;
        row-gap: toRem(10);

        
      .sf-readmore {
        height: toRem(50);
        border-top: toRem(1) solid black;
        border-bottom: toRem(1) solid black;
        box-sizing: border-box;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: toRem(18);
        text-transform: uppercase;
        font-weight: 700;

        
        &:hover {
          border-top: toRem(2) solid black;
          border-bottom: toRem(2) solid black;
        }

        .icon-arrow-right {
          font-size: toRem(25);
        }
      }

        p {
          font-size: toRem(12);
          padding: 0;
          margin: 0;
        }

        .sc-cnt-header {
          font-weight: 700;
        }
      }
    }
  }

  .stories-dropdowns {
    display: grid;
    grid-template-columns: auto 1fr auto;
    column-gap: toRem(50);
    padding: 0 toRem(32);

    .stories-dropdown {
      position: relative;
      height: 100%;
      width: auto;
      max-width: 150px;
    }

    .stories-dropdown-text {
      display: flex;
      align-items: center;

      p {
        margin: 0;
      }

      span {
        margin-left: toRem(16);
      }
    }

    .stories-dropdown-container {
      position: absolute;
      z-index: 5;

      &.sdc-right {
        right: 0;
      }
    }
  }

  .stories-arrows {
    display: flex;
    justify-content: space-between;
    margin-bottom: toRem(81);
    margin-top: toRem(36);
    padding: 0 toRem(32);
    box-sizing: border-box;

    .pointer {
      cursor: pointer;
    }

    .stories-arrows-pagination {
      display: flex;
      justify-content: space-between;

      p {
        margin: 0;
        padding: 0 toRem(10);
        line-height: toRem(36);
        cursor: pointer;

      }

    }

    span {
      font-size: toRem(36);
    }
  }

  .mobileOnly {
    display: none;
  }
}



@media only screen and (max-width: $media-mobile) {
  .stories-page {
    .mobileOnly {
      display: block;
    }
    .desktopOnly {
      display: none;
    }

    .stories-header {
      padding: toRem(50) toRem(20);
    }

    .stories-featured {
      height: auto;
      padding: 0 toRem(20);

      grid-template-columns: 1fr;

      .sf-img {
        height: toRem(225);
        margin-bottom: toRem(40);
      }

      .sf-content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        width: 100%;

        .sf-title {
          font-size: toRem(32);
          line-height: toRem(37);
          margin-bottom: toRem(33);
        }

        .sf-date {
          margin-bottom: toRem(33);
        }

        .sf-author {
          margin-bottom: toRem(21);
        }

        .sf-readmore {
          height: toRem(74);
          margin-top: toRem(40);
          margin-bottom: toRem(64);
          box-sizing: border-box;

          .icon-arrow-right {
            font-size: toRem(50);
          }

          &:hover {
            border-top: toRem(4) solid black;
            border-bottom: toRem(4) solid black;
          }
        }
      }

      .sf-cat {
        text-transform: uppercase;
      }
    }

    .stories-list {
      padding: toRem(40) toRem(20);
      grid-template-columns: 1fr;
      row-gap: toRem(30);

      .stories-item {
        height: auto;
        padding: toRem(25) 0;

        display: grid;
        grid-template-columns: 1fr;
        row-gap: toRem(22);

        .sc-img {
          position: relative;
          height: toRem(265);
        }

        .sc-cnt {
          height: 100%;
          width: 100%;
          box-sizing: border-box;

          display: grid;
          grid-template-rows: auto 1fr auto;
          row-gap: toRem(10);

          .sf-readmore {
              border-top: $border-std;
              border-bottom: $border-std;
          }

          p {
            font-size: toRem(12);
            padding: 0;
            margin: 0;
          }

          .sc-cnt-header {
            font-weight: 700;
            margin-bottom: toRem(30);
          }
        }
      }
    }

    .stories-arrows {
      span {
        font-size: toRem(36);
      }
    }
  }
}
