@import 'src/styles/variables.scss';

.main-page-loading {
  width: 100%;
  height: 70vh;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
}

.main-page {
  position: relative;
  width: 100%;
  max-width: 100vw;
  overflow: hidden;

  .main-page-banner {
    background-color: $color-highlight;
    width: 100%;
    text-align: center;
    font-size: toRem(20);
    text-transform: uppercase;
    font-weight: 700;
    
    display: flex;
    align-items: center;
    justify-content: center;
    padding: toRem(8) toRem(20);
    box-sizing: border-box;
  }

  .main-page-module {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    border-bottom: $border-std;

    &:last-child {
      border-bottom: none;
    }
  }
}
