@import 'src/styles/variables.scss';

.collections-product-row {
  display: grid;
  column-gap: 1px;
  margin-bottom: toRem(24);

  border: $border-std;
  background-color: $color-grey-2;

  .product-card {
    border: none;
    margin: 0;
  }
}

.products-loader {
  position: relative;
  height: toRem(200);
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  span {
    font-size: toRem(18);
    font-weight: 700;
  }
}

@media only screen and (max-width: $media-mobile) {
  .collections-product-row {
    display: grid;
    column-gap: 1px;
    margin-bottom: toRem(24);

    border-left: none;
    border-right: none;
    background-color: $color-grey-2;
  }

  .products-loader {
    position: relative;
    height: toRem(200);
    width: 100%;
    padding: 0 toRem(20);
    box-sizing: border-box;

    display: flex;
    align-items: center;
    justify-content: center;

    span {
      font-size: toRem(18);
      font-weight: 700;
    }
  }
}
