@import 'src/styles/variables.scss';

.product-recommendations {
  position: relative;
  min-height: toRem(720);
  width: 100%;

  .pr-header {
    height: toRem(120);
    width: 100%;
    border-bottom: $border-std;

    display: flex;
    justify-content: center;
    align-items: center;

    span {
      text-transform: uppercase;
      font-size: toRem(24);
    }
  }

  .pr-slider {
    min-height: toRem(600);
    position: relative;

    .slider-cards-group {
      height: toRem(440);
    }

    .product-card {
      min-height: toRem(240);
      grid-template-rows: toRem(324) 1fr;
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .product-recommendations {
    min-height: toRem(400);


    .pr-header {
      height: toRem(80);
      border-top: $border-std;
  
      span {
        text-transform: uppercase;
        font-size: toRem(20);
      }
    }
  
    .pr-slider {
      min-height: toRem(300);
      position: relative;

      .slider-module {
        min-height: toRem(300);

        .slider-progress {
          height: toRem(100) !important;
        }
      }
    }
  }
}
