@import 'src/styles/variables.scss';

.brands-page {
  min-height: 500px;
  position: relative;

  .brands-content {
    width: 100%;
    padding: toRem(100) toRem(150) toRem(100) toRem(36);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: toRem(135);

    .brands-letters-wrapper {
      position: relative;
      height: 100%;
      width: 100%;
    }

    .brands-letters {
      top: toRem(100);
      position: sticky;
      width: toRem(240);

      .bl-header {
        height: toRem(60);
        font-size: toRem(27);
        font-family: $font-rauna;
      }

      .bl-letters {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: toRem(30);
        font-size: toRem(20);
        line-height: toRem(20);

        .bl-letters-row {
          display: flex;
          justify-content: space-between;
          width: 100%;
        }
      }

      .bl-letter {
        color: $color-text-grey-4;
        width: toRem(16);
        text-align: center;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    .brand-list {
      .brand-list-item {
        padding-bottom: toRem(32);
        margin-bottom: toRem(48);
        border-bottom: $border-std;
        width: 100%;

        display: grid;
        grid-template-columns: 4fr 7fr 7fr;
        column-gap: toRem(30);

        &:last-child {
          border-bottom: none;
          margin-bottom: 0;
        }

        .bli-letter {
          width: toRem(105);
          height: toRem(56);
          font-size: toRem(56);
          font-family: $font-rauna;
        }

        .bli-list {
          .bli-list-item {
            margin-bottom: toRem(16);

            &.bli-list-item-padded {
              padding-left: toRem(14);
            }

            &:hover {
              text-decoration: underline;
            }
          }

          .bli-list-item-parent {
            .bli-lip-item {
                display: inline-block;
            }
            
            margin-bottom: toRem(16);
            span {
              padding-left: toRem(18);
              font-size: toRem(12);
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .brands-page {
    .brands-content {
      width: 100%;
      padding: toRem(58) toRem(20) toRem(60) toRem(20);

      display: grid;
      grid-template-columns: 1fr;

      .brands-letters {
        top: 0;
        position: relative;
        margin-bottom: toRem(80);
        width: 100%;

        .bl-header {
          height: toRem(50);
          font-size: toRem(20);
        }
      }

      .brand-list {
        .brand-list-item {
          padding-bottom: toRem(50);
          margin-bottom: toRem(45);
          border-bottom: $border-std;

          display: grid;
          grid-template-columns: auto 1fr;
          grid-template-rows: auto auto;
          column-gap: 10vw;

          &:last-child {
            padding-bottom: 0;
          }

          .bli-letter {
            width: toRem(60);
            height: toRem(50);
            font-size: toRem(60);
            line-height: toRem(60);
            font-family: $font-rauna;

            grid-row-start: 1;
            grid-row-end: 3;
          }
        }
      }
    }
  }
}
