@import 'src/styles/variables.scss';

.search {
  position: fixed;
  width: 100%;
  height: 0px;
  z-index: 3;
  box-sizing: border-box;

  left: 0;
  top: calc(#{$height-header} - #{toRem(1)});

  display: grid;
  grid-template-rows: auto 1fr;

  overflow: hidden;
  transition: height $anim-fast;
  background-color: $color-grey;
  
  .product-card {
    border: none !important;
    margin: 0 !important;
  }

  &.search-active {
    height: toRem(450);
  }

  .search-no-results{
    font-weight: 700;
  }

  .search-content {
    height: toRem(385);
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    border-bottom: $border-std;

    display: grid;
    grid-template-columns: toRem(310) 1fr;

    .search-trending {
      height: toRem(385);
      width: 100%;
      padding: toRem(32);

      box-sizing: border-box;
      border-right: $border-std;

      display: grid;
      grid-template-rows: auto 1fr;
      row-gap: toRem(30);

      p {
        padding: 0;
        margin: 0 0 1rem 0;

        font-size: toRem(14);
        line-height: toRem(14);
      }
    }
  }

  .search-products {
    position: relative;
    height: toRem(385);
    width: 100%;

    display: grid;
    background-color: $color-grey-2;
    grid-template-columns: repeat(4, 1fr);
    column-gap: toRem(1);
  }

  .search-mobile-content {
    display: none;
  }
}

@media only screen and (max-width: $media-mobile) {
  .search {
    width: 100vw;
    height: 0vh;
    top: 0;
    box-sizing: border-box;
    z-index: 100;

    background-color: white;

    &.search-active {
      height: 100vh;
    }

    .search-content {
      height: 100%;
      width: 100%;
      overflow: hidden;
      box-sizing: border-box;
      border-bottom: $border-std;

      display: grid;
      grid-template-columns: 1fr;

      .search-trending {
        display: none;
      }

      .search-products {
        display: none;
      }

      .search-mobile-content {
        display: block;
        position: relative;
        height: 100%;
        width: 100%;
        box-sizing: border-box;
        overflow-y: scroll;

        padding: toRem(25) toRem(20);

        p{
            padding: 0;
            margin: 0 0 toRem(30) 0;
            font-size: toRem(18);
        }
      }
    }
  }
}
