@import 'src/styles/variables.scss';

.header-mobile-menu {
  padding: 0 toRem(20);
  height: 100%;
  width: 100%;
  box-sizing: border-box;

  display: flex;
  justify-content: space-between;
  align-items: center;

  .header-mobile-btns {
    width: toRem(60);

    display: flex;
    justify-content: space-between;
    align-items: center;

    .header-mobile-btn {
      display: flex;
      align-items: center;
    }

    span {
      font-size: toRem(20);
    }
  }
}
