/* Fade in */
.ul-anim {
  position: relative;
}

.ul-anim-smoll {
  &.ul-anim::before {
    height: 1px;
    bottom: 2px;
  }

  &.ul-anim.active::before {
    height: 1px;
    bottom: 2px;
  }
}

.ul-anim::before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #000;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.ul-anim.active::before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #000;
  transform: scaleX(1);
  transition: transform 0.3s ease;
}

.ul-anim:hover::before {
  transform: scaleX(1);
}
