@import 'src/styles/variables.scss';

.hmm-slider {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: white;
}

.hmm-slider-image {
  position: relative;
  height: 100%;
  width: 100%;

  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.hmm-slider-tag {
  position: absolute;
  top: 0;
  left: 0;
  height: toRem(24);
  width: auto;
  padding: 0 toRem(24);

  background-color: $color-highlight;
  border: 1px solid black;

  font-family: $font-std;
  font-size: toRem(13);
  font-weight: 600;

  line-height: toRem(24);
  text-align: center;
}

.hmm-slider-arrow {
  position: absolute;
  top: calc(50% - #{toRem(25)});
  height: toRem(25);
  width: toRem(25);
  z-index: 100;
  box-sizing: border-box;
  span {
    color: black;
    font-size: toRem(25);

    transition: color $anim-fast;
    &:hover {
      color: $color-dark-grey;
    }
  }

  &.hmm-slider-arrow-right {
    right: toRem(20);
  }

  &.hmm-slider-arrow-left {
    left: toRem(20);
  }
}
