@import 'src/styles/variables.scss';

.slider-module {
  display: relative;
  min-height: toRem(540);
  overflow: hidden;
}

.slider-cards {
  background-color: $color-grey-2;
}

.slider-cards-group {
  display: grid;
  width: 100vw;
  height: toRem(540);
  column-gap: toRem(1);
  background-color: $color-grey-2;

  &.slider-cards-group-lite {
    height: toRem(385);
  }
}

.slider-progress {
  height: toRem(140);
  width: 100%;
  border-top: $border-std;

  display: flex;
  justify-content: center;
  align-items: center;

  .slider-progress-container {
    display: grid;
    width: 90%;
    max-width: toRem(580);
    height: toRem(20);
    line-height: toRem(20);
    grid-template-columns: auto 1fr auto;

    .sp-cnt {
      display: flex;
      justify-content: center;
      align-items: center;

      &.sp-cnt-disable {
        span {
          color: $color-grey-2;
        }
      }
    }

    .sp-bar {
      margin: 0 toRem(32);
      width: 100%;
      height: toRem(4);
      background-color: $color-grey-3;
      position: relative;
      display: flex;
      align-items: center;
      overflow: hidden;

      .sp-bar-amt {
        position: absolute;
        height: 100%;
        background-color: black;
        transition: left $anim-std, height $anim-fast;
      }

      &:hover {
        .sp-bar-amt {
          height: toRem(8);
        }
      }
    }

    span {
      height: toRem(20);
      font-size: toRem(20);
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .slider-progress {
    padding: 0 toRem(20);
    box-sizing: border-box;
    width: 100%;
    border-top: $border-std;
  }
}
