@import 'src/styles/variables.scss';

.email-module {
  position: relative;
  height: toRem(520);
  width: 100%;

  .email-bg {
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 100%;
    z-index: -1;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .email-mobile-bg {
    display: none;
  }

  .email-content {
    width: toRem(560);
    height: auto;
    margin: 0 auto;
    padding-top: toRem(180);

    .email-header {
      width: 100%;
      text-align: center;
      font-size: toRem(50);
      line-height: toRem(55);
      margin-bottom: toRem(40);
      font-family: $font-rauna;
    }

    .email-btn {
      display: grid;
      grid-template-columns: 1fr auto;

      .email-btn-mobile {
        display: none;
      }

      input {
        height: toRem(44);
        width: 100%;
        border: 1px solid black;
        border-radius: 0;
        padding-left: toRem(20);
        font-size: toRem(18);

        outline: none;

        &:focus-visible {
          border: 1px solid black !important;
          border-radius: 0 !important;
          outline: none;
        }
      }
    }
  }
}

@media only screen and (max-width: $media-mobile-small) {
  .email-module {
    box-sizing: border-box;
    height: toRem(650);
    padding: 0 toRem(20);

    .email-mobile-bg {
      display: block;
    }

    .email-desktop-bg {
      display: none;
    }

    .email-content {
      width: 100%;
      padding-top: toRem(220);

      .email-header {
        width: 100%;
        text-align: left;
        font-size: toRem(32);
        line-height: toRem(32);
        margin-bottom: toRem(22);
      }

      .email-btn {
        display: grid;
        grid-template-columns: 1fr;

        .email-btn-mobile {
            display: block;
            margin-top: toRem(25);
          }
          .email-btn-desktop {
            display: none;
          }

        input {
          box-sizing: border-box;
          height: toRem(35);
          width: 100%;
          border: 1px solid black;
          border-radius: 0;
          padding-left: toRem(20);
          font-size: toRem(16);

          outline: none;

          &:focus-visible {
            border: 1px solid black !important;
            border-radius: 0 !important;
            outline: none;
          }
        }
      }
    }
  }
}
