@import 'src/styles/variables.scss';

.staff-module {
  height: auto;
  position: relative;
  padding-bottom: toRem(60);
}

.staff-module-title {
  margin: toRem(70) 0 toRem(15) 0;
  text-align: center;
  width: 100%;
  font-size: toRem(20);
}

.staff-display-carousel {
  position: relative;
  width: 100%;
  height: auto;
  margin: 0 auto toRem(50) auto;
  overflow: visible;

  .car {
    overflow: visible;
  }

  .sdc-item {
    position: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: toRem(420);

    .sdc-item-picker {
      font-family: $font-rauna;
      text-align: center;
      font-size: toRem(53.6);

      transition: opacity $anim-fast, visibility $anim-fast;
    }

    .sdc-item-bg {
      position: absolute;
      width: 100%;
      height: toRem(270);
      opacity: 1;
      top: 90px;

      transition: opacity $anim-fast, height $anim-fast;

      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &.sdc-item-inactive {
      .sdc-item-picker {
        opacity: 0;
        visibility: hidden;
      }

      .sdc-item-bg {
        opacity: 0.65;
      }
    }
  }
}

.sac-author-items {
  width: 100%;
  height: auto;
  position: relative;
}

.sac-author-item-wrapper {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity $anim-fast;

  &.sac-author-placeholder {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0;
  }

  &.sacit-active {
    opacity: 1;
  }
}

.sac-item {
  width: toRem(680);
  height: toRem(200);
  position: relative;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto 1fr;

  transition: opacity $anim-fast;

  &.sac-item-disabled {
    opacity: 0;
  }

  .sac-text {
    white-space: normal !important;
    font-family: $font-rauna;

    &.sac-price {
      line-height: toRem(30);
    }

    &.sac-desc {
      font-family: $font-std;
    }
  }

  .sac-author {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-right: toRem(64);
    border-right: $border-std;

    .sac-author-content {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }

    .sac-author-img-container {
      border-radius: toRem(250);
      border: 4px solid $color-highlight;
      overflow: hidden;
      height: toRem(90);
      width: toRem(90);
      box-sizing: border-box;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .sac-content {
    width: 100%;
    height: 100%;
    background-color: white;
    padding-left: toRem(64);
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }

  .sac-buttons {
    display: flex;

    .sac-text {
      padding-right: toRem(40);
    }
  }
}

.staff-btn {
  position: absolute !important;
  font-size: toRem(30);
  bottom: toRem(100);
  z-index: 3;

  &.staff-btn-left {
    left: toRem(50);
  }

  &.staff-btn-right {
    right: toRem(50);
  }
}

@media only screen and (max-width: $media-mobile-small) {
  .staff-module-title {
    margin: toRem(50) 0 toRem(12) 0;
    font-size: toRem(15);
  }

  .staff-display-carousel {
    position: relative;
    width: 100%;
    height: auto;
    margin: 0 0 toRem(35) 0;

    .sdc-item {
      width: 100%;
      height: toRem(260);

      .sdc-item-picker {
        font-size: toRem(30);
        font-weight: 500;
      }

      .sdc-item-bg {
        width: 100%;
        height: toRem(170);
        top: toRem(60);
      }
    }
  }

  .sac-author-items {
    width: 100%;
    height: auto;
    position: relative;
  }

  .sac-item {
    width: 100%;
    height: auto;
    padding: 0;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;

    .sac-author {
      grid-row-start: 2;
      grid-row-end: 3;

      height: auto;
      padding: toRem(40) 0;
      border-right: none;

      display: flex;
      align-items: center;
      justify-content: center;

      .sac-author-content {
        height: auto;
        display: grid;
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
        column-gap: toRem(24);

        .sac-author-img-container {
          grid-row-start: 1;
          grid-row-end: 3;
          grid-column-start: 1;
          grid-column-end: 2;
        }

        .sac-text {
          grid-row-start: 1;
          grid-row-end: 2;
          grid-column-start: 2;
          grid-column-end: 3;
        }

        .sac-btn-wrapper {
          grid-row-start: 2;
          grid-row-end: 3;
          grid-column-start: 2;
          grid-column-end: 3;
          height: 100%;
        }
      }

      .sac-author-img-container {
        border-radius: toRem(250);
        border: 4px solid $color-highlight;
        overflow: hidden;
        height: toRem(90);
        width: toRem(90);
        box-sizing: border-box;

        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .sac-content {
      padding: 0 toRem(20);

      display: grid;
      grid-template-rows: auto auto auto;

      .sac-text {
        padding-bottom: toRem(15);
      }
    }

    .sac-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: toRem(25);

      .sac-text {
        padding-right: 0;
      }
    }
  }

  .staff-btn {
    font-size: toRem(30);
    bottom: unset;
    top: toRem(180);

    &.staff-btn-left {
      left: toRem(20);
    }

    &.staff-btn-right {
      right: toRem(20);
    }
  }
}
