@import 'src/styles/variables.scss';

.main-hero {
  position: relative;
  height: toRem(660);
  width: 100%;
}

.main-hero-bd-carousel {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
}

.main-hero-bd-wrapper {
  height: toRem(660);
  width: 100%;
  position: relative;
}

.main-hero-bd {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.main-hero-background-video {
  position: absolute;
  overflow: hidden;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
}

.main-hero-background-video iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
}

@media (min-aspect-ratio: 16/9) {
  .main-hero-background-video iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}
@media (max-aspect-ratio: 16/9) {
  .main-hero-background-video iframe {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}

.main-hero-bd-mobile {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: none;
}

.main-hero-content-carousel {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
}

.main-hero-content-wrapper {
  position: relative;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
}

.main-hero-content {
  position: relative;
  width: toRem(480);
  white-space: normal;
  padding-left: toRem(32);

  .main-hero-content-header {
    font-size: toRem(50);
    margin-bottom: toRem(16);
    font-family: $font-rauna;
  }

  .main-hero-content-desc {
    margin-bottom: toRem(40);
  }
}

.main-hero-controls {
  position: absolute;
  z-index: 2;
  bottom: toRem(24);
  width: 100%;

  display: inline-flex;
  align-items: center;
  justify-content: center;

  .main-hero-controls-button {
    width: toRem(100);
    height: toRem(36);
    margin: 0 toRem(4);
    position: relative;

    display: flex;
    align-items: center;

    &:hover {
      .main-hero-controls-content {
        background-color: $color-btn-gold-hover !important;
      }
    }

    .main-hero-controls-content {
      width: 100%;
      height: toRem(4);
      background-color: $color-grey-3;

      transition: background-color $anim-fast, height $anim-fast;

      &.mhc-active {
        background-color: $color-btn-gold;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile-XS) {

  .main-hero {
    display: grid;
    height: toRem(775);
    box-sizing: border-box;
  }

  .main-hero-bd-wrapper {
    height: toRem(775);
    padding-bottom: toRem(75);
    box-sizing: border-box;
    width: 100%;
  }

  .main-hero-bd {
    display: none;
  }

  .main-hero-bd-mobile {
    display: block;
    height: toRem(500);
  }

  .main-hero-bd-carousel {
    height: toRem(775);
  }

  .main-hero-content-wrapper {
    display: block;
  }

  .main-hero-content {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: auto;
    padding: 0 toRem(20);
    box-sizing: border-box;

    .main-hero-content-header {
      font-size: toRem(50);
      margin-bottom: toRem(14);
    }

    .main-hero-content-desc {
      font-size: toRem(15);
      margin-bottom: toRem(20);
    }
  }

  .main-hero-controls {
    padding: 0 toRem(20);
    box-sizing: border-box;

    .main-hero-controls-button {
      width: 100%;
      height: toRem(36);
      margin: 0 toRem(4);
      position: relative;

      display: flex;
      align-items: center;

      &:hover {
        .main-hero-controls-content {
          height: toRem(8);
          background-color: $color-btn-gold-hover !important;
        }
      }

      .main-hero-controls-content {
        width: 100%;
        height: toRem(4);
        background-color: $color-grey-3;

        transition: background-color $anim-fast, height $anim-fast;

        &.mhc-active {
          background-color: $color-btn-gold;
        }
      }
    }
  }
}
