@import 'src/styles/variables.scss';

.mobile-menu-links{
    padding: toRem(32) toRem(20) toRem(12) toRem(20) ;
    border-bottom: $border-std;
}


.mobile-menu-footer {
    padding: toRem(32) toRem(20);
    color: $color-dark-grey-3;

    .mm-ft-logo{
        width: 100%;
        max-width: toRem(185);
    }

    .mm-ft-sub {
        color: black;
        font-size: toRem(14);
        line-height: toRem(17);
    }

    .mm-ft-sm-wrapper{
        display: flex;
        width: toRem(100);
        justify-content: space-between;
        margin-top: toRem(25);
        margin-bottom: toRem(20);

        .mm-ft-sm {
            span {
                font-size: toRem(20);
            }
        }
    }

    .mm-ft-txt{
        font-size: toRem(15);
        line-height: toRem(35);
    }
    
    .mm-ft-txt-copy {
        margin-top: toRem(25);
    }
}

.mobile-menu-link {
    margin-bottom: toRem(20);
    font-size: toRem(18);
    text-transform: uppercase;
}