@import 'src/styles/variables.scss';

$width: calc(100vw - 8vw - #{toRem(475)} - 10vw);
.product-details {
  .mobile-only {
    display: none;
  }

  position: relative;
  width: 100%;
  padding-top: toRem(60);

  .pd-body {
    width: 100%;
    box-sizing: border-box;

    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: toRem(130);

    .pdb-img-placeholder {
      width: $width;
      max-width: toRem(900);
    }

    &.pd-content {
      background-color: $color-grey;
      border-top: $border-std;
      border-bottom: $border-std;
    }
  }

  .pd-title {
    height: toRem(320);
    overflow: hidden;
  }

  .pdb-content {
    min-height: 100px;
    width: toRem(475);
    margin-right: 10vw;

    &.pdb-content-selections {
      padding-top: toRem(48);
    }

    .pbdc-breadcrumb {
      font-size: toRem(14);
      color: $color-text-grey;
      font-family: $font-rauna;
      margin-bottom: toRem(55);

      .pdbc-bc-link {
        display: inline-block;
      }
    }

    .pbdc-vendor {
      font-size: toRem(14);
      margin-bottom: toRem(30);
    }

    .pbdc-title {
      font-size: toRem(27);
      line-height: toRem(30);
      margin-bottom: toRem(30);
      font-family: $font-rauna;
    }

    .pbdc-price {
      display: flex;
      font-weight: 700;
      align-items: center;

      span {
        color: $color-text-grey-2;
        text-decoration: line-through;
        margin-left: toRem(20);
        font-size: toRem(16);
      }

      font-size: toRem(25);
      line-height: toRem(28);
      margin-bottom: toRem(32);
    }

    .pdbc-cart {
      display: flex;
      justify-content: space-between;
      align-items: center;

      margin-bottom: toRem(60);

      p {
        margin: 0;
        padding: 0;
        font-size: toRem(13);
      }
    }

    .pdbc-info {
      width: 100%;
      height: auto;
      position: relative;
      margin-bottom: toRem(36);

      .pdbc-info-header {
        width: 100%;
        padding-bottom: toRem(18);
        margin-bottom: toRem(12);
        border-bottom: 1px solid black;

        .pdbc-ih {
          display: inline-block;
          font-size: toRem(20);
          font-weight: 700;
          padding-right: toRem(80);
          color: $color-text-grey-2;

          transition: color $anim-std;

          &.pdbc-ih-active {
            color: black;
          }
        }
      }

      .pdbc-info-content {
        width: 100%;
        height: auto;
        position: relative;

        .pdbc-info-item-text {
          max-height: toRem(1000);
          overflow: hidden;
          transition: max-height $anim-std;

          &.show-less {
            max-height: toRem(60);
            overflow: hidden;
          }
        }

        .pdbc-info-content-item {
          white-space: normal !important;
          position: relative;

          .pdbc-info-content-item-link {
            text-decoration: underline;
          }
        }

        p {
          width: 100%;
          margin: 0;
          padding: 0;
          word-wrap: break-word;
          white-space: normal;
          padding-bottom: toRem(12);
        }
      }
    }

    .pbdc-select-header {
      font-size: toRem(20);
      font-weight: 700;
      margin-bottom: toRem(15);
    }

    .pbdc-select-list {
      width: 100%;
      height: auto;
      margin-bottom: toRem(50);

      .pbdc-select-list-img {
        height: toRem(55);
        width: toRem(55);
        box-sizing: border-box;
        position: relative;
        display: inline-block;
        margin-right: toRem(12);

        cursor: pointer;

        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;

        mix-blend-mode: multiply;
        opacity: 0.75;

        transition: opacity $anim-fast, border $anim-fast;

        &.pbdc-select-list-img-act {
          opacity: 1;
          border: 1px solid black;
        }
      }

      .pbdc-select-list-size {
        display: inline-block;
        width: toRem(45);
        margin-right: toRem(28);
        font-size: toRem(15);
        text-align: center;

        &.pbdcsls-active {
          text-decoration: underline;
        }

        &.pbdcsls-inactive {
          text-decoration: line-through !important;
          color: $color-text-grey-2;
        }
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .product-details {
    .mobile-only {
      display: block;
    }

    .desktop-only {
      display: none;
    }

    padding-top: toRem(36);
    display: block;

    .pd-body {
      width: 100%;
      display: block;

      &.pd-content {
        background-color: transparent;
        border-top: none;
        border-bottom: none;
      }
    }

    .pd-title {
      height: auto;
      overflow: visible;
    }

    .pdb-content {
      margin-right: 0;
      padding: 0 toRem(20);
      box-sizing: border-box;
      width: 100%;

      &.pdb-content-selections {
        padding: 0 toRem(15);
      }

      .pbdc-breadcrumb {
        font-size: toRem(14);
        line-height: toRem(14);
        margin-bottom: toRem(24);

        .pdbc-bc-link {
          display: inline-block;
        }
      }

      .pbdc-vendor {
        font-size: toRem(14);
        line-height: toRem(14);
        margin-bottom: toRem(24);
      }

      .pbdc-title {
        font-size: toRem(30);
        line-height: toRem(30);
        margin-bottom: toRem(24);
        font-family: $font-rauna;
      }

      .pbdc-price {
        font-size: toRem(25);
        line-height: toRem(28);
        margin-bottom: toRem(32);
      }

      .pdbc-cart {
        display: block;
        margin-bottom: toRem(80);

        .bttn-wrapper {
          width: 100%;
          height: 42px;
          .bttn {
            width: 100%;
            height: 40px;
            .bttn-content {
              font-size: toRem(16);
              position: relative;
              padding-right: toRem(48);

              span {
                position: absolute;
                font-size: toRem(30);
                right: toRem(30);
              }
            }
          }
        }

        .pdbc-disc {
          margin-top: toRem(60);
        }

        margin-bottom: toRem(70);
        padding: 0 toRem(5);
        box-sizing: border-box;

        p {
          color: $color-text-grey-3;
          font-size: toRem(13);
          line-height: toRem(26);
          width: 100%;
          text-align: center;
          margin: 0;
          padding: 0;
        }
      }

      .pdbc-info {
        padding: 0 toRem(5);
        box-sizing: border-box;
        margin-bottom: toRem(48);

        .pdbc-info-header {
          font-size: toRem(20);
          font-weight: 700;
          padding-bottom: toRem(15);
          border-bottom: $border-std;
          margin-bottom: toRem(15);
        }

        .pdbc-info-content {
          margin-bottom: toRem(60);
        }

        .pdbc-info-content-item-link {
          text-decoration: underline;
        }

        p {
          padding: 0;
          margin: 0;
          font-size: toRem(16);
          line-height: toRem(26);
          padding-bottom: toRem(12);
        }
      }

      .pbdc-select-header {
        font-size: toRem(20);
        font-weight: 700;
        margin-bottom: toRem(15);
        padding: 0 toRem(5);
        box-sizing: border-box;
      }

      .pbdc-select-list {
        width: 100%;
        height: auto;
        margin-bottom: toRem(50);

        .pbdc-select-list-img {
          height: toRem(50);
          width: toRem(50);
          margin: 0 toRem(5);
        }

        .pbdc-select-list-size {
          margin: 0 toRem(5);
          width: toRem(50);
          text-align: left;
        }
      }
    }
  }
}
