@import 'src/styles/variables.scss';

.account-login {
  height: toRem(815);
  width: 100%;
  position: relative;
  overflow-x: hidden;

  .account-login-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 200%;
    height: 100%;
    position: absolute;
    left: 0%;
    top: 0;

    transition: left $anim-std;

    &.account-login-wrapper-new {
      left: -100%;
    }
  }

  .account-login-page {
    padding: toRem(96) 0;
    box-sizing: border-box;
    height: 100%;

    display: grid;
    grid-template-columns: 49fr 51fr;

    .alp-img {
      clip-path: polygon(
        calc(100% - 100px) 0,
        100% 100px,
        100% 100%,
        0 100%,
        0 0
      );
    }

    .alp-login {
      padding: toRem(40) toRem(100);
      box-sizing: border-box;
      width: 100%;
      position: relative;
      max-width: toRem(680);

      display: grid;
      grid-template-rows: auto 1fr auto;

      .alp-header {
        font-family: $font-rauna;
        font-size: toRem(42);
        line-height: toRem(42);
      }

      .alp-inputs-wrapper {
        width: 100%;
      }

      .alp-inputs {
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;

        .alp-input {
          margin-bottom: toRem(45);
        }

        .alp-rem {
          margin-bottom: toRem(30);
        }
      }

      .alp-selections {
        color: $color-text-grey;

        .alp-selection {
          display: flex;
          flex-wrap: wrap;
          span {
            margin-left: toRem(8);
          }
        }

        .alp-selection-forgot {
          width: auto;
        }
      }
    }
  }

  .account-new-page {
    width: toRem(725);
    margin: 0 auto;
    height: 100%;
    padding-top: toRem(90);
    padding-bottom: toRem(100);
    box-sizing: border-box;
    position: relative;

    .anp-content {
      display: grid;
      grid-template-rows: auto 1fr auto;
      height: 100%;
      width: 100%;

      .anp-header {
        width: 100%;
        text-align: center;
        font-family: $font-rauna;
        font-size: toRem(42);
      }

      .anp-form {
        display: flex;
        align-items: center;
        width: 100%;

        .anp-form-content {
          width: 100%;
          display: grid;
          grid-template-columns: 1fr 1fr;
          column-gap: toRem(20);

          .anp-form-input {
            margin-bottom: toRem(48);
          }

          .anp-form-btn {
            display: grid;
            grid-template-columns: 1fr;
            margin-bottom: toRem(18);
          }

          .anp-form-item-long {
            grid-column-start: 1;
            grid-column-end: 3;
          }
        }
      }

      .anp-return {
        width: 100%;
        text-align: center;
        display: flex;
        justify-content: center;
        color: $color-text-grey;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .account-login {
    .account-error-msg {
      text-align: center;
      margin-top: toRem(10);
    }

    min-height: toRem(1050);

    .account-login-page {
      padding: 0;
      width: 100vw;
      display: block;

      .alp-img {
        width: 100%;
        height: toRem(350);
        clip-path: polygon(
          calc(100% - 50px) 0,
          100% 50px,
          100% 100%,
          0 100%,
          0 0
        );
      }

      .alp-login {
        padding: toRem(67) toRem(55);
        max-width: 100%;

        display: grid;
        grid-template-rows: auto 1fr auto;

        .alp-header {
          font-size: toRem(30);
          line-height: toRem(30);
          margin-bottom: toRem(50);
          text-align: center;
        }

        .alp-inputs {
          .alp-input {
            margin-bottom: toRem(45);
          }

          .alp-rem {
            margin-bottom: toRem(70);
          }

          .alp-submit-btn {
            width: 100%;
            display: flex;
            justify-content: center;
          }
        }

        .alp-selections {
          text-align: center;
          margin-top: toRem(70);

          .alp-selection {
            display: flex;
            align-items: center;
            justify-content: center;
            span {
              margin-left: toRem(8);
            }
          }

          .alp-selection-forgot {
            width: auto;
          }
        }
      }
    }

    .account-new-page {
      width: 100%;
      margin: 0;
      height: 100%;
      padding: toRem(66) toRem(60) toRem(100) toRem(60);

      .anp-content {
        display: block;

        .anp-header {
          font-size: toRem(30);
          margin-bottom: toRem(50);
        }

        .anp-form {
          display: block;

          .anp-form-content {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;

            .anp-form-input {
              margin-bottom: toRem(50);
            }

            .anp-form-btn {
              margin-top: toRem(35);
              margin-bottom: 0;
              .anp-form-btn-btn {
                min-width: toRem(180) !important;
                margin: 0 auto;
              }
            }

            .anp-form-item-long {
              grid-column-start: 1;
              grid-column-end: 2;
            }
          }
        }

        .anp-return {
          margin-top: toRem(50);
        }
      }
    }
  }
}
