// @import 'src/styles/variables.scss';

// ----------------
// Functions
// ----------------
@function toRem($value) {
  $remValue: ($value / 16) + rem;
  @return $remValue;
}

// ----------------
// Animations
// ----------------
$anim-slow-ease: 1s ease-in-out;
$anim-slow: 1s ease-out;
$anim-std: 0.5s ease-out;
$anim-fast: 0.25s ease-out;
$anim-fast-ex: 0.1s;

// ----------------
// Heights
// ----------------
$height-header: toRem(60);

// ----------------
// MediaQueries
// ----------------
// @media only screen and (max-width: $media-mobile) {
$media-mobile: toRem(1000);
$media-mobile-small: toRem(850);
$media-mobile-XS: toRem(660);

// ----------------
// Fonts
// ----------------
$font-std: proxima-nova, sans-serif;
$font-rauna: ranua-regular, serif;
// font-family: $font-rauna;

// ----------------
// Colors
// ----------------
$color-grey: #f9f9f9;
$color-grey-2: #cccccc;
$color-grey-3: #f0f0f0;
$color-grey-4: #f5f5f5;
$color-grey-5: #dddddd;
$color-grey-6: #f1f1f1;

$color-highlight: #ffe6c5;

$color-btn-gold: #ffe6c5;
$color-btn-gold-hover: #ffcd8b;

$color-dark-grey: #7c7c7c;
$color-dark-grey-2: #8d8d8d;
$color-dark-grey-3: #808080;
$color-dark-grey-4: #7d7d7d;

$color-text-grey: #8f8f8f;
$color-text-grey-2: #929292;
$color-text-grey-3: #565656;
$color-text-grey-4: #848484;
$color-text-grey-5: #646464;

$border-std: 1px solid $color-grey-2;
