@import 'src/styles/variables.scss';

.search-input {
  position: relative;
  width: 100%;
  height: toRem(65);
  border-bottom: $border-std;
  padding: 0 toRem(32);
  box-sizing: border-box;


  background-color: white;

  display: grid;
  grid-template-columns: auto 1fr auto auto;
  column-gap: toRem(24);

  .search-input-content {
    display: flex;
    align-items: center;
  }

  .search-input-icon {
    span {
      font-size: toRem(28);
    }
  }

  input {
    height: 90%;
    width: 100%;
    border: none;
    border-radius: 0;
    outline: none;
    font-size: toRem(20);

    font-family: $font-std;

    &:focus {
      outline: none;
      border: none;
    }
  }

  .search-input-clear {
    opacity: 0;
    visibility: hidden;
    transition: opacity $anim-fast, visibility $anim-fast;

    &.search-input-clear-show {
      opacity: 1;
      visibility: visible;
    }

    span {
      font-size: toRem(18);
      text-transform: uppercase;
      text-decoration: underline;
    }
  }

  .search-input-exit {
    span {
      font-size: toRem(22);
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .search-input {
    height: toRem(75);
    padding: 0 toRem(20);
    column-gap: toRem(18);

    .search-input-icon {
      span {
        font-size: toRem(20);
      }
    }

    input {
      font-size: toRem(18);
    }

    .search-input-clear {
      span {
        font-size: toRem(15);
      }
    }

    .search-input-exit {
      span {
        font-size: toRem(18);
      }
    }
  }
}
