@import 'src/styles/variables.scss';

.account-details-page {
  position: relative;
  min-height: toRem(300);

  padding: toRem(60) toRem(32);
  box-sizing: border-box;

  .account-details-header {
    margin-bottom: toRem(60);

    .account-details-header-text {
      font-size: toRem(45);
      line-height: toRem(45);
      font-family: $font-rauna;
      margin-bottom: toRem(30);
    }

    .account-details-header-logout {
      font-size: toRem(18);
      text-decoration: underline;
      display: inline-block;
    }
  }

  .account-details-content {
    display: grid;
    grid-template-columns: 1fr toRem(355);
    column-gap: 9vw;

    .account-details-content-header {
      font-size: toRem(30);
      font-family: $font-rauna;
      margin-bottom: toRem(30);
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .account-details-page {
    padding: toRem(50) toRem(20) toRem(80) toRem(20);

    .account-details-header {
      margin-bottom: toRem(50);

      .account-details-header-logout {
        font-size: toRem(15);
        text-decoration: underline;
        display: inline-block;
      }
    }

    .account-details-content {
      display: block;
      .account-details-content-header {
        margin-bottom: toRem(25);
      }
    }
  }
}
