@import 'src/styles/variables.scss';

.collections-page {
  position: relative;
  min-height: 500px;
  width: 100%;

  .collections-header {
    display: grid;
    grid-template-columns: 1fr auto;
    column-gap: toRem(1);

    &.collections-header-full {
      border-bottom: $border-std;
      margin-bottom: toRem(50);
      background-color: $color-grey-2;

      .collection-header-text {
        background-color: $color-grey;
      }

      .collection-header-image {
        width: 60vw;
        height: toRem(500);
      }
    }

    .collection-header-text {
      padding: toRem(80) toRem(42);
      width: 100%;
      height: 100%;
      box-sizing: border-box;

      display: flex;
      align-items: center;

      h1 {
        margin: 0;
        padding: 0;
        font-size: toRem(50);
        line-height: toRem(50);
        margin-bottom: toRem(20);
      }

      h3 {
        margin: 0;
        padding: 0;
        font-size: toRem(16);
        line-height: toRem(27);
        font-weight: normal;
        font-family: $font-std;
        max-width: toRem(480);
      }
    }
  }

  .collections-content-header {
    margin-bottom: toRem(40);
    padding-right: toRem(32);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: 1fr auto;

    .icon-filter-close {
      font-size: toRem(16.5);
    }

    .collections-content-header-filters {
      display: grid;
      grid-template-columns: auto 1fr;
      column-gap: toRem(50);

      .cchf-group {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: toRem(358);
        margin: toRem(8) 0;

        transition: width $anim-fast;

        &.cchf-group-small {
          width: toRem(225);
        }
      }

      .cchf-clear {
        line-height: toRem(34);
        text-decoration: underline;
        text-transform: uppercase;
        font-size: toRem(18);
      }

      .cchf-tags {
        .cchf-filter-tag {
          margin: toRem(8) toRem(22) toRem(8) 0;

          p {
            font-size: toRem(14);
            font-family: $font-std;
          }
        }
      }
    }
  }

  .collections-content {
    width: 100%;
    padding: 0 toRem(32);
    box-sizing: border-box;

    .collections-content-body {
      margin-bottom: toRem(90);
      height: auto;

      display: grid;
      grid-template-columns: auto 1fr;
    }

    .collections-product-wrapper {
      position: relative;
      width: 100%;
      height: auto;
    }
  }

  .collections-product-arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .icon-arrow-btn-left,
    .icon-arrow-btn-right {
      font-size: toRem(35);
      transition: opacity $anim-fast, visibility $anim-fast;

      &.cpa-hide {
        opacity: 0 !important;
        visibility: hidden !important;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .collections-page {
    .collections-header {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;
      column-gap: 0;

      &.collections-header-full {
        border-bottom: none;
        margin-bottom: toRem(30);

        .collection-header-text {
          grid-row-start: 2;
          grid-row-end: 3;
          background-color: $color-grey;
          padding-bottom: toRem(60);
        }

        .collection-header-image {
          grid-row-start: 1;
          grid-row-end: 2;
          width: 100%;
          height: toRem(350);
          border-bottom: $border-std;
        }
      }

      .collection-header-text {
        padding: toRem(45) toRem(20) toRem(40) toRem(20);

        h1 {
          margin-bottom: toRem(30);
        }

        h3 {
          font-size: toRem(15);
          line-height: toRem(24);
          max-width: toRem(480);
        }
      }
    }

    .collections-content-header {
      margin-bottom: toRem(24);
      padding-right: toRem(20);
    }

    .collections-content {
      width: 100%;
      padding: 0;
      box-sizing: border-box;

      .collections-content-body {
        margin-bottom: toRem(100);
        grid-template-columns: 1fr;
      }
    }

    .collections-product-arrows {
      position: relative;
      padding: 0 toRem(20);
      box-sizing: border-box;
      top: toRem(40);
      display: flex;
      justify-content: space-between;
      align-items: center;

      .icon-arrow-btn-left,
      .icon-arrow-btn-right {
        font-size: toRem(40);
        transition: opacity $anim-fast, visibility $anim-fast;
        
        &.cpa-hide {
          opacity: 0 !important;
          visibility: hidden !important;
        }
      }
    }

    .cchf-tags {
      display: none;
    }

    
    .cchf-group {
      width: toRem(225) !important;
    }

    .collections-content-header-filters {
      column-gap: toRem(15) !important;
    }
  }
}
