@import 'src/styles/variables.scss';

.header {
  position: fixed;
  height: $height-header;
  width: 100%;
  top: 0;
  background-color: $color-grey;
  border-bottom: $border-std;
  box-sizing: border-box;
  z-index: 10;

  .header-desktop {
    padding: 0 toRem(30);
    height: 100%;
    width: 100%;
    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-mobile {
    display: none;
  }
}

@media only screen and (max-width: $media-mobile) {
  .header {
    .header-desktop {
      display: none;
    }

    .header-mobile {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}
