@import 'src/styles/variables.scss';

.mobile-megamenu {
  width: 100%;
  height: auto;
  margin-bottom: toRem(25);
  box-sizing: border-box;

  .mmm-header {
    width: 100%;
    height: toRem(30);
    padding: 0 toRem(20);
    box-sizing: border-box;

    display: grid;
    grid-template-columns: 1fr auto;

    .mmm-header-txt {
      font-size: toRem(25);
      text-transform: uppercase;
    }

    .mmm-header-icon {
      display: flex;
      padding-left: toRem(32);
      align-items: center;
    }
  }
}
