@import 'src/styles/variables.scss';

.cart-content-items {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  padding: 0 toRem(24);
  border-bottom: $border-std;
  padding-top: toRem(36);
  
  .cart-empty {
    height: 100%;
  }

  .cci-product {
    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: toRem(25);
    margin-bottom: toRem(60);

    .ccip-img {
      height: toRem(100);
      width: toRem(100);
      background-size: contain;
    }

    .ccip-title {
      font-family: $font-rauna;
      font-size: toRem(15);
      line-height: toRem(24);
    }

    .ccip-var {
      font-size: toRem(15);
      line-height: toRem(18);
      color: $color-text-grey-4;
    }

    .ccip-price {
      margin: toRem(10) 0 toRem(13) 0;
      font-family: $font-rauna;
      font-size: toRem(15);
      line-height: toRem(18);

      display: flex;
      align-items: center;

      span {
      font-size: toRem(13);
      line-height: toRem(13);
      margin-left: toRem(16);
      text-decoration: line-through;
      color: $color-text-grey-2;
      }
    }

    .ccip-btns {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .ccip-btns-inc {
        display: flex;
        align-items: center;

        p {
          margin: 0;
          padding: 0;
          width: 6ch;
          text-align: center;
          height: toRem(24);
          line-height: toRem(24);
        }

        .ccip-btn {
          height: toRem(20);
          width: toRem(20);
          span {
            font-size: toRem(20);
            font-weight: 700;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: $media-mobile-XS) {
  .cart-content-items {
    padding: 0 toRem(20);
    padding-top: toRem(50);
  
    .cci-product {
      .ccip-btns {
        display: flex;
        justify-content: space-between;
        align-items: center;
  
        .ccip-btns-inc {
          display: flex;
          align-items: center;
  
          p {
            width: 4ch;
          }
        }
      }
    }
  }
}