@import 'src/styles/variables.scss';

.cart-checkout {
  height: toRem(120);
  width: 100%;
  padding: 0 toRem(28);
  box-sizing: border-box;

  display: flex;
  align-items: center;
  justify-content: center;

  transition: opacity $anim-std;
  opacity: 1;

  &.cart-checkout-empty {
    opacity: 0;
  }
}

@media only screen and (max-width: $media-mobile-XS) {
  .cart-checkout {
    padding: 0 toRem(20);
    .bttn-wrapper {
      width: 100%;
      .bttn {
        width: 100%;
        .bttn-content {
          justify-content: center;
          padding-right: toRem(80);

          span {
            position: absolute;
            right: toRem(60);
          }
        }
      }
    }
  }
}
