@import 'src/styles/variables.scss';

.cart-content-total {
  padding: toRem(32) toRem(24) 0 toRem(24);

  .cct-sub {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: toRem(40);
    font-size: toRem(20);
    font-family: $font-rauna;
  }

  .cct-shipping {
    font-size: toRem(14);
    color: $color-text-grey-2;
    margin-bottom: toRem(40);
  }

  .cct-return-header {
    font-family: $font-rauna;
    font-size: toRem(21);
    margin-bottom: toRem(12);
  }

  .cct-return-text {
    font-size: toRem(14);
    line-height: toRem(20);
    margin-bottom: toRem(12);
  }

  .cct-return-link {
    font-size: toRem(14);
    line-height: toRem(20);
    text-decoration: underline;
  }
}

@media only screen and (max-width: $media-mobile-XS) {
  .cart-content-total {
    padding: toRem(32) toRem(20) 0 toRem(20);
  }
}
