@import 'src/styles/variables.scss';
.generic-pages {
  position: relative;

  h1 {
    font-size: toRem(53);
    font-family: $font-rauna;
    margin: 0 0 toRem(96) 0;
  }

  h2 {
    font-family: $font-rauna;
    padding-bottom: toRem(32);
    font-size: toRem(35);
    border-bottom: 1px solid black;
  }

  h3 {
    font-family: $font-rauna;
    font-size: toRem(22);
    margin: 0 0 toRem(20) 0;
  }

  a {
    color: black;
  }
}

@media only screen and (max-width: $media-mobile) {
  .generic-pages {
    h1 {
      font-size: toRem(50);
      margin: 0 0 toRem(50) 0;
    }
  }
}
