@import 'src/styles/variables.scss';

.input {
  width: 100%;
  height: auto;
  position: relative;

  .input-header {
    font-size: toRem(17);
    font-family: $font-rauna;
    margin-bottom: toRem(5);
  }

  .input-entry {
    margin: toRem(4) 0;
    height: toRem(36);
    width: 100%;
    border: none;
    border-bottom: $border-std;
    box-sizing: border-box;
    font-size: toRem(16);
    line-height: toRem(36);
    outline: none;

    transition: border $anim-fast;

    &:focus-visible {
      border-bottom: 1px solid black !important;
      border-radius: 0 !important;
      outline: none;
    }
  }
}
