@import 'src/styles/variables.scss';

.account-order-history-content {
  position: relative;
  min-height: 200px;
  width: 100%;
  padding-bottom: toRem(50);
  box-sizing: border-box;

  .loader-container {
    margin: toRem(20) 0;
    width: 100%;
    height: 50px;
    position: relative;
    top: 0;
    left: 0;
    pointer-events: none;
  }

  .aohc-view-all {
    margin-top: toRem(50);
    text-align: center;
    text-decoration: underline;
  }

  .aohc-table {
    text-align: left;
    border-collapse: collapse;

    .aohc-table-header {
      border-bottom: $border-std;
      box-sizing: border-box;

      th {
        padding-bottom: toRem(10);
      }
    }

    td {
      padding: toRem(10) 0;
      font-size: toRem(18);
      text-transform: capitalize;
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .account-order-history-content {
    .aohc-view-all {
      margin-top: toRem(50);
      text-align: center;
      text-decoration: underline;
    }

    .aohc-table {
      text-align: left;
      border-collapse: collapse;
      border-spacing: toRem(25) 0;

      .hide-mobile {
        display: none;
      }

      .aohc-table-header {
        th {
          padding-bottom: toRem(10);
        }
      }

      td {
        font-size: toRem(14);
      }
    }
  }
}
