@import 'src/styles/variables.scss';

.sizing-page {
  padding: toRem(120) toRem(32);

  .sizing-content {
    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: 7.8vw;
  }

  .sizing-items {
    height: auto;
    width: toRem(250);
    position: sticky;
    top: toRem(100);

    .si-item {
      cursor: pointer;
      font-size: toRem(20);
      margin-bottom: toRem(17);

      &.si-item-parent {
        cursor: unset;
        font-size: toRem(25);
        margin-bottom: toRem(20);
      }
    }
  }

  .sizing-list {
    padding-right: toRem(95);
    width: 100%;
    box-sizing: border-box;

    .si-divier {
      margin: toRem(90) 0;
      height: 1px;
      background-color: $color-grey-2;
    }

    .si-list {
      display: grid;
      grid-template-columns: toRem(240) 1fr;
      column-gap: toRem(20);

      img {
        max-width: 700px;
        width: 100%;
        height: auto;
        margin: 0 auto;
      }

      span {
        font-size: toRem(30);
        font-family: $font-rauna;
      }
    }
  }
}

@media only screen and (max-width: $media-mobile) {
  .sizing-page {
    .sizing-btn {
      height: toRem(50);
      width: toRem(50);
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $color-highlight;
      border-radius: toRem(50);

      span {
        font-size: toRem(20);
      }
    }

    .mobile-only {
      display: block;
      position: fixed;
      bottom: toRem(20);
      right: toRem(20);
    }

    padding: toRem(50) toRem(20);

    .sizing-content {
      display: block;
      column-gap: 0;
    }

    .sizing-items {
      width: 100%;
      position: relative;
      top: 0;
      margin-bottom: toRem(128);
    }

    .sizing-list {
      padding-right: 0;
      width: 100%;

      .si-divier {
        margin: toRem(50) 0;
      }

      .si-list {
        display: block;

        img {
          margin-top: toRem(40);
          max-width: 100%;
          width: 100%;
        }
      }
    }
  }
}
