@import 'src/styles/variables.scss';

.mobile-menu-content {
  position: absolute;
  top: 0;
  left: -400px;
  max-width: toRem(400);
  width: 85vw;
  height: 100vh;
  z-index: 20;
  transition: left $anim-std;

  background-color: $color-grey;

  &.mobile-menu-content-active {
      left: 0;
  }

  .mobile-menu-body{
    height: calc(100vh - #{$height-header});
    overflow-y: auto;
  }

  .mobile-megamenus {
      padding: toRem(20) 0 0 0;
      box-sizing: border-box;
      border-bottom: $border-std;
  }
}
