@import 'src/styles/variables.scss';

.cart-header {
  width: 100%;
  height: toRem(90);
  padding: 0 toRem(24);
  box-sizing: border-box;
  border-bottom: $border-std;

  display: flex;
  justify-content: space-between;
  align-items: center;

  .cart-header-icon {
    height: toRem(18);
  }

  p {
    padding: 0;
    margin: 0;
    font-size: toRem(22);
    font-family: $font-rauna;
  }

  span {
    font-size: toRem(18);
    line-height: toRem(18);
  }
}

@media only screen and (max-width: $media-mobile-XS) {
  .cart-header {
    height: toRem(111);
    padding: 0 toRem(20);

    p {
      font-size: toRem(28);
    }

    span {
      font-size: toRem(20);
      line-height: toRem(20);
    }
  }
}
