@import 'src/styles/variables.scss';

.collections-sort-by {
  position: relative;
  display: flex;
  align-items: center;

  .csb-text {
    display: flex;
    align-items: center;
    margin: toRem(8) 0;

    p {
      font-size: toRem(18);
      padding: 0;
      margin: 0;
      text-transform: uppercase;
    }

    span {
      padding-left: toRem(15);
      font-size: toRem(13);
    }
  }

  .csb-dropdown {
    position: absolute;
    z-index: 5;
    right: 0;
    top: 100%;
    min-width: 100%;
    width: auto;
  }
}
