@import 'src/styles/variables.scss';

.tag-wrapper {
  min-width: toRem(100);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0 toRem(24);
  
  transition: filter $anim-std;

  &:hover {
    filter: brightness(95%)
  }

  p {
    margin: 0;
    padding: 0 toRem(12) 0 0;
    font-size: toRem(18);
    font-family: $font-rauna;
  }

  span {
    font-size: toRem(12);
    padding-left: toRem(0);
  }
}
